import { takeLatest, put, call, all, select } from 'redux-saga/effects';
import { getContractDetails } from '../services/api';
import moment from 'moment';

function* watcherSaga() {
  yield takeLatest('GET_CONTRACT_DATA', workerGetContractData);
}

function* workerGetContractData() {
  let contractDetails = yield call(getContractDetails);

  let details = JSON.parse(contractDetails.details);

  console.log({ details });
  let obj = {};

  details.address.forEach(item => {
    obj[item.address] = item.value;
  });

  let vote = { yes: 0, no: 0 };
  for (let key in obj) {
    if (obj[key] < 1000000) {
      vote.no++;
    } else {
      vote.yes++;
    }
  }

  console.log({ vote });

  details.address.sort((a, b) => {
    return b.date - a.date;
  });

  let data = details.address.map(item => {
    return {
      date: moment(item.date).format('hh:mm DD.MM.YY'),
      address: `${item.address.substr(0, 11)}...${item.address.substr(-11)}`,
      value: (item.value / 1e6).toLocaleString('en-IN', {
        maximumFractionDigits: 6,
        minimumFractionDigits: 6,
      }),
      type: item.value < 1000000 ? 'gone' : 'come',
      key: item.date,
    };
  });

  yield all([
    put({
      type: 'LIST_VOTING',
      val: data,
    }),
    put({
      type: 'VOTE',
      val: vote,
    }),
  ]);

  console.log({ data });

  try {
  } catch (error) {
    console.log({ error });
  }
}

export default watcherSaga;
