import React from 'react';
import connect from 'react-redux/es/connect/connect';
import * as actions from '../actions/action';
import Voting from '../components/Voting';
import { VIDEO, CONTRACT } from '../const';

class Page extends React.PureComponent {
  state = {};

  render() {
    return (
      <>
        <div className="videovote">
          <Voting />

          <div className="videovote__left">
            <p className="videovote__tit">
              #MetaHash voting
              <br /> smartcontracts review video
            </p>
            <a
              href={`https://youtube.com/watch?v=${VIDEO}`}
              className="videovote__link"
              target="_blank">
              youtube.com/watch?v={VIDEO}
            </a>
            <iframe
              className="videovote__player"
              width="360"
              height="203"
              src={`https://www.youtube.com/embed/${VIDEO}`}
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            />
            <div className="videovote__over-list">
              <p className="videovote__how-tit">How to vote?</p>
              <ul className="videovote__list">
                <li>1 TMH or more for YES</li>
                <li>less than 1 TMH for NO</li>
              </ul>
              <p className="videovote__area-tit">
                Send a transaction from your MHC wallet to smartcontract
                address:
              </p>
              <div className="videovote__overarea">
                <button className="videovote__area-btn" onClick={this.copyCont}>
                  <svg
                    width="15"
                    height="15"
                    viewBox="0 0 15 15"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg">
                    <path
                      d="M14.6589 11.9078H4.59888V5.01239e-06H11.1687L14.7 3.77767V11.9078H14.6589ZM6.40559 10.0601H12.8112V4.47569L10.3475 1.80669H6.40559V10.0601Z"
                      fill="white"
                    />
                    <path
                      d="M10.1011 14.659H-5.01239e-06V2.75114H4.59889V4.55783H1.84776V12.8112H8.25337V11.9078H10.1011V14.659Z"
                      fill="white"
                    />
                    <path
                      d="M11.0044 3.65444H7.34998V4.5578H11.0044V3.65444Z"
                      fill="white"
                    />
                    <path
                      d="M11.9489 5.46117H7.34998V6.36452H11.9489V5.46117Z"
                      fill="white"
                    />
                    <path
                      d="M11.9489 7.30894H7.34998V8.2123H11.9489V7.30894Z"
                      fill="white"
                    />
                    <path
                      d="M4.59887 6.40557H2.7511V7.30893H4.59887V6.40557Z"
                      fill="white"
                    />
                    <path
                      d="M5.50222 8.2123H2.7511V9.11566H5.50222V8.2123Z"
                      fill="white"
                    />
                    <path
                      d="M5.50222 10.0601H2.7511V10.9634H5.50222V10.0601Z"
                      fill="white"
                    />
                  </svg>
                  <span>copy address</span>
                </button>
                <textarea
                  className="videovote__area"
                  disabled=""
                  value={CONTRACT}
                  readOnly
                />
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  copyCont() {
    let input = document.createElement('input');
    input.setAttribute('value', CONTRACT);
    document.body.appendChild(input);
    input.select();
    let result = document.execCommand('copy');
    document.body.removeChild(input);
  }
}

function mapS({ reduser }) {
  return {};
}
export default connect(
  mapS,
  actions
)(Page);
