import { Map } from 'immutable';

const init = {
  listVoting: [],
  vote: { yes: 0, no: 0 },
};

const reducer = function(state = Map(init), action) {
  switch (action.type) {
    case 'LIST_VOTING':
      return state.set('listVoting', action.val);
    case 'VOTE':
      return state.set('vote', action.val);

    default:
      return state;
  }
};

export default reducer;
