import axios from 'axios';
import { CONTRACT } from '../const';

export const getContractDetails = () => {
  return new Promise((resolve, reject) => {
    axios({
      headers: { 'Content-Type': 'text/plain;charset=UTF-8' },
      method: 'post',
      url: 'http://torv8.net-dev.metahashnetwork.com:5795',
      data: {
        id: 1,
        params: {
          address: CONTRACT,
          type: 2,
        },
        method: 'get-contract-details',
        pretty: true,
      },
    })
      .then(response => {
        resolve(response.data.result);
      })
      .catch(error => {
        reject(error);
      });
  });
};
