import React from 'react';
import connect from 'react-redux/es/connect/connect';
import * as actions from '../actions/action';

class Voting extends React.PureComponent {
  componentDidMount() {
    this.props.getContractData();

    setInterval(this.props.getContractData,3000)
  }

  render() {
    const { vote } = this.props;
    return (
      <div className="videovote__right">
        <p className="videovote__right-tit">Do you like my video?</p>

        <div className="videovote__progress">
          <div className="progress-bar" style={{ width: `${this.getPr()}%` }} />
        </div>
        <div className="videovote__afterprog">
          <span className="videovote__afterprog yes">Yes ({vote.yes})</span>
          <span className="videovote__afterprog no">No ({vote.no})</span>
        </div>
        <div className="videovote__overtab">
          <table className="videovote__table">
            <tbody>{this.getVotings()}</tbody>
          </table>
        </div>
      </div>
    );
  }

  getVotings = () => {
    const { listVoting } = this.props;

    return listVoting.map(item => {
      return (
        <tr key={item.key} className={item.type}>
          <td>{item.date}</td>
          <td>{item.address}</td>
          <td>{item.value} TMH</td>
        </tr>
      );
    });
  };

  getPr = () => {
    const { yes, no } = this.props.vote;
    let all = yes + no;
    return (yes / all) * 100;
  };
}

function mapS({ reduser }) {
  return {
    listVoting: reduser.get('listVoting'),
    vote: reduser.get('vote'),
  };
}
export default connect(
  mapS,
  actions
)(Voting);
